.MoraCount {
	height: 15vh;
	display: flex;
}

.MoraCount button {
	flex-grow: 1;
	background: rgb(177, 177, 177);
	color: black;
	text-transform: uppercase;
	font-weight: bold;
}

.MoraCount button:hover {
	opacity: 0.9;
}

.MoraCount button:disabled {
	opacity: 0.5;
	cursor: not-allowed;
}
