.PitchGraph {
	position: relative;
	height: 60vh;
	background: lightgrey;
}

.PitchGraph div {
	height: 50%;
}

.PitchGraph .Word {
	position: absolute;
	top: 20px;
	left: 50%;
	transform: translateX(-50%);
	z-index: 100;
	font-size: 2rem;
	color: white;
	font-weight: bold;
}

.PitchGraph .MoraCollection {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 20%;
	height: 60%;
	width: 100%;
	z-index: 1000;
}

.HighPitch,
.LowPitch {
	position: relative;
	font-weight: bold;
}

.HighPitch p,
.LowPitch p {
	position: absolute;
	padding: 10px;
	font-size: 1.2rem;
}

.HighPitch p {
	bottom: 5%;
}

.LowPitch p {
	top: 5%;
}

.PlayButton {
	position: absolute;
	bottom: 20px;
	left: 50%;
	transform: translateX(-50%);
	padding: 10px;
	color: white;
	text-transform: uppercase;
	font-weight: bold;
}

.PlayButton:hover {
	opacity: 0.8;
}
