.Mora {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;

	width: 10%;
	font-size: 3rem;
	color: white;

	border: 5px solid white;
	animation: FadeIn 1s forwards;
}

.Mora.High {
	transform: translateY(-50%);
	transition: all 0.3s;
}

.Mora.Low {
	transform: translateY(50%);
	transition: all 0.3s;
}

@keyframes FadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
