.Particle {
	position: relative;
	margin-left: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	width: 10%;

	font-size: 3rem;
	color: white;
	border: 5px dashed white;

	animation: FadeIn 1s forwards;
}

.Particle.High {
	transform: translateY(-50%);
	transition: all 0.3s;
}

.Particle.Low {
	transform: translateY(50%);
	transition: all 0.3s;
}

@keyframes FadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
